<!-- eslint-disable max-len -->
<template>
   <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        3 
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <Preloader v-if="orderLoading" />
          <div class="cart__data" v-else>
            <BaseFormText v-model="formData.name" :error="formError.name" title="" placeholder="   " />
            <BaseFormText v-model="formData.address" :error="formError.address" title=" " placeholder="  " />
            <BaseFormText v-model="formData.phone" :error="formError.phone" title="" placeholder="  " type="tel"/>
            <BaseFormText v-model="formData.email" :error="formError.email" title="Email" placeholder="  Email" type="email"/>
            <BaseFormTextarea title="  " v-model="formData.comment" :error="formError.comment" placeholder=" "/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                     <b>500 </b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>
        <OrderSummary :formErrorMessage="formErrorMessage" :products="products" :totalPrice="totalPrice"/>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import OrderSummary from '@/components/OrderSummary.vue';
import axios from 'axios';
import Preloader from '@/components/Preloader.vue';
import { API_BASE_URL } from '../config';

export default {
  components: {
    BaseFormText, BaseFormTextarea, OrderSummary, Preloader,
  },
  data() {
    return {
      formData: {},
      formError: {},
      orderLoading: false,
      formErrorMessage: '',
    };
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts' }),
    ...mapGetters({ totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    order() {
      this.orderLoading = true;
      this.formError = {};
      this.formErrorMessage = '';
      clearTimeout(this.loadOrderTimer);
      this.loadOrderTimer = setTimeout(() => {
        axios
          .post(`${API_BASE_URL}/api/orders`, {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
          .then((response) => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          // eslint-disable-next-line no-return-assign
          .then(() => this.orderLoading = false);
      }, 2000);
    },
  },
};
</script>
